@media (max-width: 600px) {
  .App {
    -webkit-flex-direction: column;
            flex-direction: column;
  }
  .App-container-1 {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-flex: 2 1;
            flex: 2 1;
    background-color: #c6c6c6;
  }
  .App-container-1 h3 {
    display: -webkit-inline-flex;
    display: inline-flex;
    /* text-align: left; */
    /* font-weight: medium; */
    font-size-adjust: 45px;
    padding-left: 2vw;
  }
  .App-container-1 h4 {
    display: -webkit-inline-flex;
    display: inline-flex;
    text-align: left;
    font-weight: 300;
    padding-left: 1vw;
